export type FilterType = {
    market : string,
    banner : string[],
    period : string,
    startDate : string,
    endDate : string,
    compareDateRange: string[],
    isCustomCompareChecked: boolean
}


const masterFilterColumn : FilterType = {
    market : 'HK',
    banner : ['KFC'],
    period : 'custom',
    startDate :  moment().subtract(7,'days').format('YYYY-MM-DD'),
    endDate: moment().format('YYYY-MM-DD'),
    compareDateRange: [moment().subtract(15,'days').format('YYYY-MM-DD'),moment().subtract(8,'days').format('YYYY-MM-DD')],
    isCustomCompareChecked : false
}

export const landingDataReducer = (state = _masterData, action: any) => {
    switch(action.type){
        case FETCH_MASTER_DATA:
            return _masterData
        case FETCH_PROFILES_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{profiles: {isLoading: action.payload, data: state.profiles.data }}}
            }
            return {...state, ...{profiles: {...state.profiles, data:action.payload}} }
        case FETCH_GENDERS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{genders: {isLoading: action.payload, data: state.genders.data }}}
            }
            return {...state, ...{genders: {...state.genders, data:action.payload}} }
        case FETCH_AGERANGES_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{ageRanges: {isLoading: action.payload, data: state.ageRanges.data }}}
            }
            return {...state, ...{ageRanges: {...state.ageRanges,data:action.payload}} }
        case FETCH_RECORDS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{records: {isLoading: action.payload, data: state.records.data }}}
            }
            return {...state, ...{records: {...state.records, data: action.payload}} }
        case FETCH_TICKETS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{tickets: {isLoading: action.payload, data: state.tickets.data }}}
            }
            return {...state, ...{tickets: {...state.tickets, data:action.payload}} }
        case FETCH_SALESAMOUNTS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{salesAmounts: {isLoading: action.payload, data: state.salesAmounts.data }}}
            }
            return { ...state, ...{ salesAmounts: { ...state.salesAmounts,data:action.payload } } }
        case FETCH_TRAFFICS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{traffics: {isLoading: action.payload, data: state.traffics.data }}}
            }
            return { ...state, ...{ traffics: { ...state.traffics ,data:action.payload} } }
        case FETCH_TRAFFICSOURCES_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{trafficSources: {isLoading: action.payload, data: state.trafficSources.data }}}
            }
            return { ...state, ...{ trafficSources: { ...state.trafficSources, data:action.payload } } }
        case FETCH_FUNNELS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{funnels: {isLoading: action.payload, data: state.funnels.data }}}
            }
            return { ...state, ...{ funnels: { ...state.funnels, data:action.payload} } }
        case FETCH_CONVERSIONS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{conversions: {isLoading: action.payload, data: state.conversions.data }}}
            }
            return { ...state, ...{ conversions: { ...state.conversions, data: action.payload } } }
        case FETCH_MEMBERS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{members: {isLoading: action.payload, data: state.members.data }}}
            }
            return { ...state, ...{ members: { ...state.members, data:action.payload } } }
        case FETCH_PURCHASES_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{purchases: {isLoading: action.payload, data: state.purchases.data }}}
            }
            return { ...state, ...{ purchases: { ...state.purchases, data: action.payload } } }
        case FETCH_TOPS_DATA:
            if(typeof(action.payload) === 'boolean'){
                return {...state, ...{topDatas: {isLoading: action.payload, data: state.topDatas.data }}}
            }
            return { ...state, ...{ topDatas: { ...state.topDatas } } }
        default:
            return state
    }
}
export const handleLandingFilterReducer = (state = masterFilterColumn, action: any) => {
    switch(action.type){
        case HANDLE_MASTER_DATA_CHANGE:
            const data = {[action.payload.column]:action.payload.value}
            return {...state,...data}
        case HANDLE_MASTER_DATA_DATE_CHANGE:
            return {...state,...action.payload}
        case HANDLE_CUSTOM_COMPARE_CKECK:
            return {...state, isCustomCompareChecked: !state.isCustomCompareChecked}
        case HANDLE_CUSTOM_COMPARE_DATE_RANGE:
            const dateRange: string[] = action.payload
            return {...state, compareDateRange: dateRange }
        default:
            return state
    }
}


export const handleLandingDetailsReducer = (state = {}, action: any) => {
    switch(action.type){
        case FETCH_LANDING_DETAILS:
            return action.payload
        default:
            return state
    }
}

export const isLoading = (state = false, action: any) => {
    switch(action.type){
        case LOADING:
            return action.payload
        default:
            return state
    }
}

export const landginTab = (state = 0, action: any) => {
    switch(action.type){
        case LANDINGTAB:
            return action.payload
        default:
            return state
    }
}




