import { combineReducers } from 'redux';
import
    {
        userReducer,
        isAdminLoading
    }
from './userReducer'
import { 
    themeReducer,
    themeSelectedReducer
} from './themeReducer'

import {
    loginReducer
} from './loginReducer'

import {
    audienceReducer,
    audienceDetails,
    audienceFilterReducer,
    audienceCreateFilterReducer,
    audienceTagReducer,
    audienceFormReducer,
    isAudienceLoading,
    audienceFilterSelectorReducer
} from './audienceReducer'

import {
    landingDataReducer,
    handleLandingFilterReducer,
    handleLandingDetailsReducer,
    isLoading,
    landginTab
} from './landingReducer'

export const rootReducer = combineReducers({
    user: userReducer,
    theme: themeReducer,
    themeSelected: themeSelectedReducer,
    loginUser: loginReducer,
    audience: audienceReducer,
    audienceDetails: audienceDetails,
    audienceFilter: audienceFilterReducer,
    audienceCreator: audienceCreateFilterReducer,
    audienceFilterSelecetor: audienceFilterSelectorReducer,
    audienceTag: audienceTagReducer,
    audienceForm: audienceFormReducer,
    landingData: landingDataReducer,
    landingFilter: handleLandingFilterReducer,
    landingDetails: handleLandingDetailsReducer,
    isLoading: isLoading,
    isAudienceLoading: isAudienceLoading,
    isAdminLoading: isAdminLoading,
    landginTab: landginTab
})

export type RootState = ReturnType<typeof rootReducer>