export const audienceReducer = (state = [], action: any) => {
    switch(action.type){
        case FETCH_AUDIENCE:
            return action.payload
        case DUPLICATED_AUDIENCE:
            //will remove when interact with backend
            return [...state, action.payload]
        default:
            return state
    }
}


export const audienceFilterReducer = (state = audienceFilter, action: any) => {
    switch(action.type){
        case HANDLE_AUDIENCE_FILTER:
            if (action.payload.confirm === null)
            {
                return { ...state, ...{confirm:''} }
            }
            return { ...state, ...action.payload }
        default:
            return state
    }
}

export const audienceCreateFilterReducer = (state = createFilter, action: any) => {
    switch(action.type){
        case FETCH_AUDIENCE_CREATOR:
            return state
        default:
            return state
    }
}

export const audienceTagReducer = (state:tagItem[] = [], action: any) => {
    switch (action.type)
    {
        case FETCH_AUDIENCE_TAG:
            const tags: tagItem[] = action.payload
            return tags
        default:
            return state
    }
}


export const audienceFormReducer = (state = audienceForm, action: any) => {
    switch (action.type)
    {
        case INIT_AUDIENCE_FORM:
            return action.payload as audienceForm
        case UNMOUNT_AUDIENCE_FORM:
            return audienceForm
        case HANDLE_AUDIENCE_FORM_NAME:
            return { ...state, ...{ name: action.payload } }
        case HANDLE_AUDIENCE_FORM_BANNER:
            return { ...state, ...{ banner: action.payload } }
        case HANDLE_AUDIENCE_FORM_TAGS_AFTER_EDIT:
            const tags = _.sortBy([...state.tags.filter(x=>x.id !== action.payload.id), action.payload],'id')
            return { ...state, ...{ tags: tags } }
        case HANDLE_AUDIENCE_FORM_TAGS:
            const isExisted = _.includes(state.tags.map(x=>x.id), action.payload.id)
            if (isExisted)
            {
                const target = state.tags.filter(x => x.id !== action.payload.id)
                return { ...state, ...{ tags: target } }
            }
            else
            {
                const target = [...state.tags, action.payload as tagItem]
                return { ...state, ...{ tags: target } }
            }
        case HANDLE_AUDIENCE_FORM_SCHEDULE:
            return { ...state, ...{ schedule: action.payload } }
        
        case HANDLE_AUDIENCE_FORM_ACTION:
            return { ...state, ...{ actionDestination: action.payload }}
        case HANDLE_AUDIENCE_FORM_FILTERING_ADD:
            return { ...state, ...{ filtering: [...state.filtering, action.payload] } }
        case HANDLE_AUDIENCE_FORM_FILTERING_REMOVE:
            return { ...state, ...{ filtering: state.filtering.filter(x => x.id !== action.payload) } }
        case HANDLE_AUDIENCE_FORM_FILTERING_CHANGE:
            const payload = _.sortBy([...state.filtering.filter(x=>x.id !== action.payload.id), action.payload],'id')
            return { ...state, ...{ filtering: payload}}
        default:
            return state
    }
}

export const audienceDetails = (state = {}, action: any) =>
{
    switch(action.type){
        case FETCH_SPECIFIC_AUDIENCE:
            return action.payload
        default:
            return state
    }
    
}

export const audienceFilterSelectorReducer = (state = audienceSelectorMapping, action: any) => {
    switch(action.type){
        case AUDIENCE_FILTER_SELECTOR:
            return state
        default:
            return state
    }
}

export const isAudienceLoading = (state = false, action: any) => {
    switch(action.type){
        case AUDIENCE_LOADING:
            return action.payload
        default:
            return state
    }
}