import {
    FETCH_USER,
    ADMIN_LOADING,
    HANDLE_USER_CHANGE
} from '../actions/type'

import _ from 'lodash'

export type UserType = {
    user_id: string;
    name: string;
    email: string;
    roles: string;
    brand: string[];
    area: string;
    is_reset: boolean;
    updated_time: string;
    created_time: string;
}


export const userReducer = (state:UserType[] = [], action: any) => {
    switch (action.type)
    {
        case FETCH_USER:
            return action.payload

        case HANDLE_USER_CHANGE:
            const specificUser = _.find(state, x=> x.user_id === action.payload.user_id)
            if(specificUser)
            {
                const modifyUser = [...state.filter(x => x.user_id !== action.payload.user_id), action.payload]
                console.log(modifyUser)
                return _.orderBy(modifyUser, x=>x.user_id)
            }
            else
            {
                return state
            }
        default:
            return state
    }
}


export const isAdminLoading = (state = false, action: any) => {
    switch(action.type){
        case ADMIN_LOADING:
            return action.payload
        default:
            return state
    }
}

export default userReducer;