import React,{lazy, Suspense} from "react";
import { Route, Switch, withRouter, useLocation} from "react-router-dom"
import Header from './components/header'

const admin = lazy(() => import('./pages/admin'));
const login = lazy(() => import('./pages/login'));

//landing
const landingPages = lazy(() => import('./pages/landings'));
const recordPages =  lazy(() => import('./pages/landings/moreDetails/Records'));
const genderPages = lazy(() => import('./pages/landings/moreDetails/Gender'));
const ageRangePages = lazy(() => import('./pages/landings/moreDetails/AgeRange'));
const trafficPages = lazy(() => import('./pages/landings/moreDetails/Traffic'));
const ticketPages = lazy(() => import('./pages/landings/moreDetails/Ticket'));
const salesAmountsPages = lazy(() => import('./pages/landings/moreDetails/SalesAmount'));
const trafficSourcePages = lazy(() => import('./pages/landings/moreDetails/TrafficSource'));
const conversionsPages = lazy(() => import('./pages/landings/moreDetails/Conversion'));
const memberPages = lazy(() => import('./pages/landings/moreDetails/Member'));
const purchasePages = lazy(() => import('./pages/landings/moreDetails/Purchase'));


//audience
const audiencePages = lazy(() => import('./pages/audience'));
const audienceDetailPages = lazy(() => import('./pages/audience/AudienceDetailPages'));
const audienceEditPages = lazy(() => import('./pages/audience/AudienceEditPages'));
const audienceEditSchedulePages = lazy(() => import('./pages/audience/AudienceEditScheulePages'));
const audienceEditConfirmPages = lazy(() => import('./pages/audience/AudienceEditConfirmPages'));
const audienceEditActionPages = lazy(() => import('./pages/audience/AudienceEditActionPages'));
const createAudiencePages = lazy(() => import('./pages/audience_new'));
const scheduleAudiencePages = lazy(() => import('./pages/audience_schedule'));
const audienceConfirmPages = lazy(() => import('./pages/audience_confirm'));
const audienceActionPages = lazy(() => import('./pages/audience_action'));
//practice
const dnd = lazy(() => import('./pages/dnd'));

const App: React.FunctionComponent = () =>
{
   const location = useLocation();
  //const renderFor404Routes = () => (<Redirect to='/book'/>);
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <div className="page" >
        {location.pathname === '/' ? '' : <Header />}
        <Switch>
          <Route exact path="/" component={login}/>
          <Route exact path="/audience" component={audiencePages} />
          <Route exact path="/audience/new" component={createAudiencePages}/>
          <Route exact path="/audience/new/schedule" component={scheduleAudiencePages} />
          <Route exact path="/audience/new/confirm" component={audienceConfirmPages} />
          <Route exact path="/audience/new/action" component={audienceActionPages} />
          <Route exact path="/audience/:id" component={audienceDetailPages} />
          <Route exact path="/audience/:id/edit" component={audienceEditPages} />
          <Route exact path="/audience/:id/edit/schedule" component={audienceEditSchedulePages} />
          <Route exact path="/audience/:id/edit/confirm" component={audienceEditConfirmPages} />
          <Route exact path="/audience/:id/edit/action" component={audienceEditActionPages} />
          
          
          <Route exact path="/admin" component={admin}/>
          <Route exact path="/login" component={login} />
          
          <Route exact path="/dashboard" component={landingPages}/>
          <Route exact path="/dashboard/records" component={recordPages} />
          <Route exact path="/dashboard/genders" component={genderPages} />
          <Route exact path="/dashboard/ageranges" component={ageRangePages} />
          <Route exact path="/dashboard/tickets" component={ticketPages} />
          <Route exact path="/dashboard/salesamounts" component={salesAmountsPages} />
          <Route exact path="/dashboard/traffics" component={trafficPages} />
          <Route exact path="/dashboard/trafficsources" component={trafficSourcePages} />
          <Route exact path="/dashboard/conversions" component={conversionsPages} />
          <Route exact path="/dashboard/members" component={memberPages} />
          <Route exact path="/dashboard/purchases" component={purchasePages} />
          
          <Route exact path="/dnd" component={dnd}/>
        </Switch>
      </div>
    </Suspense>
  );
};

export default withRouter(App);